<template>
<div>
    <h5 @click="clickList(블로그.number)">{{블로그.title}}</h5>
    <p>{{블로그.date}}</p>
</div>


</template>

<script>
export default {
    name : 'List',
    props : {
        블로그글 : Array,
        블로그 : Object
    },
    methods: {
        clickList (data) {
            this.$router.push({ path: '/detail/'+data});
        }
    },
}
</script>

